<template>
    <div class="create-reim">
        <el-steps v-if="requestion.state!=0" class="state-wrapper" :active="requestion.state" align-center>
            <el-step v-for="step in steps" :key="step.title" :title="step.title" :description="step.desc"></el-step>
        </el-steps>
        <el-form v-if="!!requestion" class="requestion-wrapper" v-model="requestion" label-width="0.8rem" :inline="true">
            <el-form-item label=":">
                {{requestion.name}}
            </el-form-item>
            <el-form-item label=":">
                {{requestion.laboratory}}
            </el-form-item>
            <el-form-item label=":">
                <span>{{requestion.project}}</span>
            </el-form-item>
            <el-form-item label=":">
                <span>{{formatDate(requestion.occurTime)}}</span>
            </el-form-item>
        </el-form>
        <reim-wrapper :requestion="requestion" :reims="reims" :pics="pics" @addNewReim="addNewReim" @deleteReim="deleteReim" @removeInvoice="removeInvoice" :isRemark="isRemark"></reim-wrapper>
        <el-dialog
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="remarkClose">
            <template>
                <el-radio v-model="remarkResult" :label="0"></el-radio>
                <el-radio v-model="remarkResult" :label="1"></el-radio>
            </template>
            <el-input
                v-if="!!remarkResult"
                type="textarea"
                autosize
                placeholder=""
                v-model="remarkReason">
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="remark"> </el-button>
            </span>
        </el-dialog>
        <div class="operate-wrapper">
            <el-button v-if="(requestion.state>=3&&requestion.state<4)&&isRemark" type="primary" class="submit" @click="dialogVisible=true"></el-button>
            <el-button v-if="requestion.state===4&&isRemark" type="primary" class="submit" @click="endInvoice"></el-button>
        </div>
    </div>
</template>
<script>
import {formatDate, query} from '@/utils'
import ReimWrapper from '@/components/ReimWrapper'
import {steps} from '@/dataMap'
export default {
    components: {
        ReimWrapper
    },
    data() {
        return {
            steps,
            select: '',
            opts: [],
            requestion: {},
            reims: [],
            isRemark: false,
            dialogVisible: false,
            remarkResult: 0,
            remarkReason: '',
            pics: []
        }
    },
    created() {
        let step = location.href.split('?')
        let param = query(step[1])
        if (!!param.isRemark) {
            this.isRemark = true
        }
        this.getRequestionDetail(param.id, this.getInvoices)
    },
    methods: {
        formatDate,
        endInvoice() {
            this.$request
                .post('/test/approveRequestion')
                .send({
                    id: this.requestion.id,
                    uid: this.$store.state.user.id,
                    state: this.requestion.state,
                    operate: 0,
                    reason: ''
                })
                .end((err, res) => {
                    if (!!err) {
                        console.log(err)
                    } else {
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                        this.getRequestionDetail(this.requestion.id)
                    }
                })
        },
        removeInvoice(index) {
            this.pics.splice(index, 1)
        },
        getInvoices() {
            this.$request
                .post('/test/getInvoices')
                .send({
                    requestion: this.requestion.id
                })
                .end((err, res) => {
                    if (!!err) {
                        console.log(err)
                    } else {
                        this.pics = res.body
                    }
                })
        },
        remarkClose() {
            this.dialogVisible = false
        },
        remark() {
            this.$request
                .post('/test/approveRequestion')
                .send({
                    id: this.requestion.id,
                    uid: this.$store.state.user.id,
                    state: this.requestion.state,
                    operate: this.remarkResult,
                    reason: this.reason
                })
                .end((err, res) => {
                    if (!!err) {
                        console.log(err)
                    } else {
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                        this.getRequestionDetail(this.requestion.id)
                    }
                })
            this.dialogVisible = false
        },
        getRequestionDetail(id, callback = undefined) {
            this.$request
                .post('/test/getRequestionDetail')
                .send({
                    id: id
                })
                .end((err, res) => {
                    if (!!err) {
                        console.log(err)
                    } else {
                        this.requestion = res.body
                        if (!!callback) {
                            callback()
                        }
                    }
                })
            this.$request
                .post('/test/getReimbursements')
                .send({
                    id: id
                })
                .end((err, res) => {
                    if (!!err) {
                        console.log(err)
                    } else {
                        this.reims = res.body
                    }
                })
        },
        selectReq(val) {
            this.requestion = this.opts[val]
        },
        addNewReim(rem) {
            if (this.$store.state.user.id === +this.requestion.requester) {
                if (!rem) {
                    this.$message({
                        type: 'error',
                        message: ''
                    })
                } else {
                    rem.startDate = new Date(rem.startDate).getTime()
                    rem.startTime = new Date(rem.startTime).getTime()
                    rem.endDate = new Date(rem.startDate).getTime()
                    rem.endTime = new Date(rem.endTime).getTime()
                    this.$request
                        .post('/test/addReimbursement')
                        .send({
                            requestion: this.requestion.id,
                            uid: this.$store.state.user.id,
                            ...rem
                        })
                        .end((err, res) => {
                            if (!!err) {
                                console.log(err)
                            } else {
                                this.$message({
                                    type: 'success',
                                    message: ''
                                })
                                this.reims.push(rem)
                            }
                        })
                }
            } else {
                this.$message({
                    type: 'error',
                    message: ''
                })
            }
        },
        deleteReim(id, index) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$request
                    .post('/test/deleteReim')
                    .send({
                        id: id
                    })
                    .end((err, res) => {
                        if (!!err) {
                            console.log(err)
                        } else {
                            this.reims.splice(index, 1)
                            this.$message({
                                type: 'success',
                                message: '!'
                            })
                        }
                    })
            })
        }
    }
}
</script>
<style lang="stylus">
.create-reim
    text-align: left
    .state-wrapper
        margin-top: .3rem
        margin-bottom: .5rem
    .requestion-wrapper
        border-bottom: 1px solid #F2F6FC
    .label
        font-size: .14rem
    .el-form
        font-size: .14rem
        .el-form-item
            min-width: 30%
    .operate-wrapper
        width: 100%
        margin-top: .2rem
</style>



